(function() {
  angular.module('morano2017', ['ngAnimate', 'ngTouch', 'ngSanitize', 'ui.router', '720kb.socialshare']);

}).call(this);


(function() {
  angular.module('morano2017').controller('MainQuoteController', function($state, $stateParams, sentence) {
    this.sentence = sentence;
    return this.url = $state.href('main.quote', $stateParams, {
      absolute: true
    });
  });

}).call(this);


(function() {
  angular.module('morano2017').controller('MainController', function($state, facts) {
    this.bg = 'nadine-' + Math.floor(Math.random() * 5);
    this.generate = function() {
      var i, idx, key, p, params;
      params = {
        p1: null,
        p2: null,
        p3: null
      };
      for (p = i = 1; i <= 3; p = ++i) {
        key = 'p' + p;
        while (params[key] === null) {
          idx = Math.floor(Math.random() * facts[key].length);
          if (facts[key][idx] !== '') {
            params[key] = idx;
          }
        }
      }
      return $state.go('main.quote', params);
    };
    return null;
  });

}).call(this);


(function() {
  angular.module('morano2017').run(function($log) {
    'ngInject';
    return $log.debug('runBlock end');
  });

}).call(this);


(function() {
  angular.module('morano2017').config(function($stateProvider, $urlRouterProvider) {
    'ngInject';
    $urlRouterProvider.otherwise('/');
    return $stateProvider.state('main', {
      url: '/',
      templateUrl: 'app/main/main.html',
      controller: 'MainController',
      controllerAs: 'main',
      resolve: {
        facts: function($http) {
          return $http.get('assets/facts.json').then(function(r) {
            return r.data;
          });
        }
      }
    }).state('main.quote', {
      url: ':p1|:p2|:p3',
      templateUrl: 'app/main/quote/quote.html',
      controller: 'MainQuoteController',
      controllerAs: 'quote',
      resolve: {
        sentence: function($stateParams, facts) {
          var p;
          p = $stateParams;
          return [facts.p1[p.p1], facts.p2[p.p2], facts.p3[p.p3]].join(' ');
        }
      }
    });
  });

}).call(this);


(function() {
  angular.module('morano2017').constant('moment', moment);

}).call(this);


(function() {
  angular.module('morano2017').config(function($logProvider) {
    'ngInject';
    return $logProvider.debugEnabled(true);
  });

}).call(this);


angular.module("morano2017").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<div class=\"main\"><img class=\"main__bg\" ng-src=\"assets/images/{{ main.bg }}.jpg\"><div class=\"main__credit\">Site parodique par <a href=\"https://twitter.com/ThePixelHunt\" target=\"_blank\">@ThePixelHunt</a> &amp; <a href=\"https://twitter.com/pirhoo\" target=\"_blank\">@Pirhoo</a></div><div ui-view=\"\"><div class=\"lead main__lead text-center\"><p>Générez instantanément la prochaine phrase culte de Nadine Morano.</p><p><button class=\"btn btn-primary btn-lg\" ng-click=\"main.generate()\">Générez</button></p></div></div></div>");
$templateCache.put("app/main/quote/quote.html","<div class=\"main__quote text-center\"><p class=\"main__quote__sentence\">❝&nbsp;{{ quote.sentence }}&nbsp;❞</p><p><button class=\"btn btn-primary btn-lg\" ng-click=\"main.generate()\">Générez une autre phrase</button> <span class=\"\"><button socialshare=\"\" socialshare-text=\"❝{{ quote.sentence }}❞\" socialshare-url=\"{{ quote.url }}\" socialshare-provider=\"twitter\" class=\"btn btn-outline btn-lg\"><i class=\"fa fa-fw fa-twitter\"></i> <span class=\"sr-only\">Twitter</span></button> <button socialshare=\"\" socialshare-text=\"❝{{ quote.sentence }}❞\" socialshare-url=\"{{ quote.url }}\" socialshare-provider=\"facebook\" socialshare-type=\"feed\" socialshare-via=\"732431740195590\" socialshare-redirect-uri=\"http://www.morano-2017.com/close.html\" class=\"btn btn-outline btn-lg\"><i class=\"fa fa-fw fa-facebook\"></i> <span class=\"sr-only\">Facebook</span></button></span></p></div>");}]);